@import "sass/index";

$inputLeftMargin: 30px;
$defaultMargin: 20px;

.box {
  width: 100%;
  margin-top: $defaultMargin;
}

.formBox {
  width: 100%;
  margin: 0;
  padding: 0;
  border: none;
}

.title {
  text-transform: uppercase;
  font-size: 0.875rem;
  margin: 0;
  color: use(--template-input-text-color);

  @media (max-width: 450px) {
    font-size: 0.685rem;
  }
}

.checkbox {
  margin: 0;
  margin-left: spacing(-2.4);
  color: use(--template-input-text-color);
}

.check {
  color: use(--template-input-text-color);
}

label.label,
.label {
  color: use(--template-input-text-color);
}

.inputWrapper {
  position: relative;

  & :first-child {
    margin-top: 0;
  }
}

.rootInput {
  width: calc(100% - #{$inputLeftMargin * 2}) !important;
  margin: 0;
  margin-left: $inputLeftMargin;
  height: 40px;

  padding: 12px;
  border-radius: 4px;
  border: none;
  font-size: 1rem;
  font-family: "Gilroy";
  outline: none;
  background: white;

  &:focus {
    border: 2px solid #{use(--secondary-main)};
  }
}
